@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}

.fomo-section {
  .fomo-block {
    max-width: 350px;
    width: 100%;
  }
  .fomo-image {
    flex: unset;
    @include margin-end(15px);
  }
  .fomo-wrapper{z-index: 97;}
  .fomo-wrapper .fomo-block{
    display: block;
    transition: .25s all ease-in-out;
    padding: 15px;
    transform: translateX(100%);
  }
  .fomo-content{white-space: nowrap;}
  .fomo-wrapper .fomo-block:not(.active){position: absolute; pointer-events: none;}
  .fomo-block.active{transform: translateX(0%); position: static;}
}